}

.project-description {
    font-size: 1rem; /* Description size */
    color: #767676; /* Slightly lighter color for description */
    margin: 10px 0; /* Vertical spacing */
}

.project-skills {
    margin-top: 15px;
    margin-bottom: 25px; /* Space below skills */
}

.project-skill {
    border: 1px solid #009999; /* Blue background for skills */
    color: #888888; /* White text */
    padding: 5px 10px; /* Padding for skills */
    border-top-right-radius: 7px; /* Rounded corners for skills */
    border-bottom-left-radius: 7px;
    margin-right: 5px; /* Space between skills */
    font-size: 0.9rem; /* Skill size */
    transition: background-color 0.3s ease; /* Smooth background color transition */
}

.project-skill:hover {
    border: 1px solid #004c4c; /* Darker blue on hover for skills */

}

.project-button {
    display: inline-block; /* Button styling */
    padding: 10px 15px; /* Padding for button */
    background-color: #42b3c2; /* Button color */
    color: #032023; /* Button text color */
    border-radius: 40%; /* Rounded corners for button */
    text-decoration: none; /* Remove underline */
    transition: background-color 0.3s ease, transform 0.3s ease; /* Button transitions */
    font-weight: 500;
}

.project-button:hover {
    background-color: #0d707d; /* Darker blue on hover */
    transform: scale(1.1); /* Lift effect on button hover */
    color:#e5ffff;
}

/* Contact Section */
.contact-section {
    padding: 40px 20px;
    text-align: center;
    background-color: black;
    min-height: 100vh;
}

.contact-title {
    color: cyan;
    font-family: 'sans-serif';
    margin-top: 30px;
    font-size: 50px;
    margin-bottom: 3%;
    margin-left: 5%;}

.contact-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

/* Social Icons */
.social-icons {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

.social-icon {
    text-decoration: none;
    color: #333;
    font-size: 1.2rem;
}

.social-icon img {
    width: 30px;
    height: 30px;
    margin-right: 8px;
    vertical-align: middle;
}

.social-icon span {
    vertical-align: middle;
}

/* Contact Form */
.contact-form {
    width: 100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

.form-input {
    width: 100%;
    padding: 12px 15px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
}

.form-input:focus {
    border-color: #007bff;
}

.send-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 20px;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.send-button:hover {
    background-color: #0056b3;
}

/* Footer */
.footer {
    margin-top: 40px;
    padding: 20px 0;
    background-color: #333;
    color: white;
    text-align: center;
    width: 100%;
}

.footer p {
    margin: 0;
}

.footer-icons {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.footer-icons a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.footer-icons a:hover {
    color: #007bff;
}

/* Responsive Styles */
@media (min-width: 768px) {
    .contact-content {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
    }

    .social-icons {
        margin-bottom: 0;
    }
}

@media (max-width: 768px) {
    .footer-icons {
        flex-direction: column;
    }
}

